class ListComponent {
    constructor(hospitalService) {
        this.hospitalService = hospitalService;
        this.isLoader = false;
        this.page = 0;
        this.limit = 10;
        this.active = true;
        this.totalCount = 0;
    }
    ngOnInit() {
        this.get();
    }
    clickActive() {
        this.active = !this.active;
        this.get();
    }
    get() {
        this.isLoader = true;
        this.hospitalService.get(this.page, this.limit, this.active)
            .subscribe((res) => {
            console.log(res);
            if (res.status == '200') {
                this.isLoader = false;
                this.hospitals = res.data.docs;
                this.totalCount = res.data.c;
                console.log(this.hospitals);
            }
            else {
                this.isLoader = false;
                this.hospitals = [];
                console.log('empty');
            }
        });
    }
    imageUrl(url) {
        return src_environments_environment__WEBPACK_IMPORTED_MODULE_1__["environment"].base_url + url;
    }
}


