class ProfileComponent {
    constructor(hospitalService, router, authService, chatService, activatedRoute, notifyService) {
        this.hospitalService = hospitalService;
        this.router = router;
        this.authService = authService;
        this.chatService = chatService;
        this.activatedRoute = activatedRoute;
        this.notifyService = notifyService;
        this.isLoader = false;
        this.tab = 1;
    }
    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            console.log(params['id']);
            if (params['id']) {
                this.get(params['id']);
            }
        });
    }
    onclickTab(tab) {
        this.tab = tab;
    }
    get(id) {
        this.isLoader = true;
        this.hospitalService.getById(id).subscribe((res) => {
            console.log(res);
            if (res.status == '200') {
                this.isLoader = false;
                this.hospital = res.data;
            }
            else {
                this.router.navigate(['app/hospitals']);
            }
        });
    }
    chat() {
        this.chatService.addfriend(this.hospital.user._id).subscribe((res) => {
            if (+res.status === 200) {
                this.router.navigate([`app/inbox/${this.hospital.user._id}`]);
            }
        });
    }
    get ImageSrc() { return src_environments_environment__WEBPACK_IMPORTED_MODULE_1__["environment"].base_url + this.hospital.user.image; }
    markAsActive() {
        this.authService.toggleactive(this.hospital.user._id).subscribe((res) => {
            if (+res.status === 200) {
                this.hospital.user.active = !this.hospital.user.active;
                this.notifyService.success(res.data.msg);
            }
        });
    }
    // [routerLink]="['/app/doctor/edit/', doctor._id]"
    goToEdit() {
        // this.router.navigate(['/app/hospitals/edit/', this.hospital._id], { queryParams: { returnUrl: this.router.url } });
        this.router.navigate(['/app/hospitals/edit/', this.hospital.user._id]);
    }
}


